/* 🎀 整體樣式，保持畫面置中 🎀 */
.App {
  text-align: center;
  background-color: #ffe4e1; /* 粉色背景 */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin);
  color: #ff69b4;
  font-family: 'Comic Sans MS', 'Poppins', 'Quicksand', sans-serif;
  position: relative; /* 讓右上角按鈕的絕對定位不影響其他元素 */
}

/* 🎀 修正標題置中 🎀 */
.title {
  font-size: 28px;
  font-weight: bold;
  color: #ff1493;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(255, 105, 180, 0.5);
}

/* 🎀 保持按鈕區域置中 🎀 */
.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
}

/* 🎀 右上角區域 (連接錢包 + 切換鏈) 🎀 */
.top-right-bar {
  display: flex;
  align-items: center;
  gap: 10px;
  position: absolute;
  top: 15px;
  right: 20px;
}

/* 🎀 錢包連接按鈕 + 切換鏈按鈕 (確保樣式一致) 🎀 */
.top-button {
  background: linear-gradient(to right, #ff9a9e, #fad0c4);
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

/* 🎀 按鈕懸停效果 🎀 */
.top-button:hover {
  transform: scale(1.1);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2);
}

/* 🎀 NFT 鑄造按鈕 (確保原有樣式) 🎀 */
.cute-button {
  background: linear-gradient(to right, #ff9a9e, #fad0c4);
  border: none;
  border-radius: 20px;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  margin: 10px;
}

.cute-button:hover {
  transform: scale(1.1);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2);
}

.mint-button {
  margin-top: 10px;
}

.mint-button:disabled {
  background: #cccccc;
  cursor: not-allowed;
}

/* 🎀 Loading 樣式 (轉圈圈) 🎀 */
.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(255, 105, 180, 0.3);
  border-top: 4px solid #ff69b4;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-top: 10px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 🎀 修正下拉選單顏色問題 🎀 */
select.top-button {
  appearance: none; /* 移除系統默認樣式 */
  padding: 10px 20px;
  background: linear-gradient(to right, #ffb6c1, #ff69b4);
  color: white; /* 確保初始文字是白色 */
  border-radius: 20px;
  outline: none;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

/* 🎀 設置下拉選項顏色 🎀 */
select.top-button option {
  background: white;
  color: black;
  font-size: 14px;
  font-weight: normal;
}

/* 🎀 當選擇框聚焦時，顯示高亮邊框 🎀 */
select.top-button:focus {
  border: 2px solid #ff69b4;
}

/* 🎀 調整下拉選單滑鼠懸停時的顏色 🎀 */
select.top-button:hover {
  transform: scale(1.1);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2);
}

/* 🎀 連接錢包後的選單 🎀 */
.menu {
  position: absolute;
  top: 40px; /* 讓選單顯示在錢包地址下方 */
  left: 0;
  background-color: #ff9a9e;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  width: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  z-index: 10;
}

.menu button {
  background: #ffe4e1;
  border: none;
  border-radius: 10px;
  padding: 8px 16px;
  font-size: 14px;
  color: #ff69b4;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.menu button:hover {
  transform: scale(1.1);
  background-color: #ff1493;
  color: white;
}